import React, { useState } from "react";
import { observer } from "mobx-react-lite";

import { useStores } from "../../hooks/useStores";

export const GameNew = observer(() => {
  const {
    game: {
      minTricks,
      setMinTricks,
      maxTricks,
      setMaxTricks,
      players,
      addPlayer,
      removePlayer,
      startGame
    }
  } = useStores();
  const [name, setName] = useState("");

  const savePlayer = () => {
    addPlayer(name);
    setName("");
  };

  return (
    <div className="card" style={{ marginTop: "1rem" }}>
      <h5 className="card-header">Nieuw spel</h5>
      <div className="card-body">
        <div className="row">
          <div className="col-sm">
            <div className="input-group mb-3">
              <input
                type="text"
                className="form-control"
                placeholder="Naam"
                value={name}
                onChange={e => setName(e.target.value)}
              />
              <div className="input-group-append">
                <button
                  className="btn btn-primary"
                  type="button"
                  onClick={savePlayer}
                >
                  Toevoegen
                </button>
              </div>
            </div>
          </div>
        </div>
        <div className="table-responsive-xl">
          <table className="table table-striped">
            <thead>
              <tr>
                <th scope="col" colSpan={2}>
                  Spelers
                </th>
              </tr>
            </thead>
            <tbody>
              {players.length ? (
                players.map(player => (
                  <tr key={player.id}>
                    <td>{player.name}</td>
                    <td style={{ width: "10%" }}>
                      <button
                        className="btn btn-sm btn-danger"
                        type="button"
                        onClick={() => removePlayer(player.id)}
                      >
                        Verwijderen
                      </button>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td>Nog geen spelers toegevoegd.</td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
        <div className="row">
          <div className="col">
            <div className="form-group">
              <label htmlFor="minTricks">Minimaal aantal slagen</label>
              <input
                type="number"
                className="form-control"
                id="minTricks"
                value={minTricks}
                onChange={e => setMinTricks(parseInt(e.target.value, 10))}
              />
            </div>
          </div>
          <div className="col">
            <div className="form-group">
              <label htmlFor="maxTricks">Maximaal aantal slagen</label>
              <input
                type="number"
                className="form-control"
                id="maxTricks"
                value={maxTricks}
                onChange={e => setMaxTricks(parseInt(e.target.value, 10))}
              />
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col">
            <button
              type="button"
              className="btn btn-primary btn-lg btn-block"
              onClick={startGame}
            >
              Beginnen
            </button>
          </div>
        </div>
      </div>
    </div>
  );
});
